export class OperatingExpenseModel {
	id: number;
	financialValue: string;
	note: string;
	balance: string;
	expenseDate: string;

	clear() {
		this.id = 0;
		this.financialValue = '';
		this.note = '';
		this.balance = '';
		this.expenseDate = new Date().toISOString().substr(0, 16);
	}
}
