// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of, Observable } from 'rxjs';
import { delay, map, startWith } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';
import { OperatingExpenseModel } from '../models/operating-expenses.model';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'gas-operating-expenses-edit-dialog',
	templateUrl: 'operating-expenses-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class OperatingExpensesEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	userOperatingExpense: OperatingExpenseModel;
	expenseForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	myControl = new FormControl();
	filteredOptions: Observable<any[]>;
	selectedPumpId = '';
	isFinalOperatingExpense = false;
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<OperatingExpensesEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<OperatingExpensesEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private layoutUtilsService: LayoutUtilsService,
		private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.userOperatingExpense = this.data.expense;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	createForm() {
		let date = this.userOperatingExpense.expenseDate;
		if (this.userOperatingExpense.id > 0)
			date = new Date(+new Date(this.userOperatingExpense.expenseDate) - this.tzoffset).toISOString().substr(0, 16);

		this.expenseForm = this.fb.group({
			id: [this.userOperatingExpense.id, Validators.required],
			financialValue: new FormControl(this.userOperatingExpense.financialValue, Validators.required),
			note: [this.userOperatingExpense.note, Validators.required],
			expenseDate: [date, Validators.required],
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.userOperatingExpense.id > 0) {
			return `تعديل الحركة`;
		}

		return 'اضافة حركة';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.expenseForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */

	/**
	 * Returns prepared customer
	 */
	prepareOperatingExpense(): OperatingExpenseModel {
		const controls = this.expenseForm.controls;
		const _expense = new OperatingExpenseModel();
		_expense.id = this.userOperatingExpense.id;
		_expense.financialValue = controls.financialValue.value;
		_expense.note = controls.note.value;
		_expense.expenseDate = controls.expenseDate.value;

		return _expense;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.expenseForm.controls;

		/** check form */
		if (this.expenseForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedOperatingExpense = this.prepareOperatingExpense();
		if (editedOperatingExpense.id > 0) {
			this.updateOperatingExpense(editedOperatingExpense);
		} else {
			this.createOperatingExpense(editedOperatingExpense);
		}
	}

	/**
	 * Update customer
	 *
	 * @param _expense: OperatingExpenseModel
	 */
	updateOperatingExpense(_expense: OperatingExpenseModel) {
		let expenseDate = new Date(+new Date(_expense.expenseDate) - this.tzoffset).toISOString().slice(0, 16);

		this.http.put(
			globalVariables.baseUrl + 'api/Expenses',
			{
				..._expense,
				financialValue: +_expense.financialValue,
				expenseDate: expenseDate,
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _expense, isEdit: true });
			}, err => {
				let msg = 'فشل في تعديل الحركة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create customer
	 *
	 * @param _expense: OperatingExpenseModel
	 */
	createOperatingExpense(_expense: OperatingExpenseModel) {
		let expenseDate = new Date(+new Date(_expense.expenseDate) - this.tzoffset).toISOString().slice(0, 16);

		this.http.post(
			globalVariables.baseUrl + 'api/Expenses',
			{
				..._expense,
				financialValue: +_expense.financialValue,
				expenseDate: expenseDate
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _expense, isEdit: false });
			}, err => {
				let msg = 'فشل في اضافة الحركة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
